.expenseDate{
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #2a2a2a;
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;

}
.expenseDay{
    font-size: 1.5rem;
    font-weight: bold;
}
.expenseMonth{
    font-size: 0.75rem;
    font-weight: bold;
}
.expenseYear{
    font-size: 0.75rem;
}